@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    outline: 0;
}
a {
    text-decoration: none;
}
.App_App__16ZpL {
    display: grid;
    grid-template-rows: 60px auto 50px;
    grid-template-columns: 230px 1fr;
    grid-column-gap: 0.5rem;
    column-gap: 0.5rem;
    grid-row-gap: 0.5rem;
    row-gap: 0.5rem;
    text-align: center;
    height: 100vh;
}
.NavBarForm_NavBar__3Cyof {
    grid-row: 1;
    grid-column: 1 / span 4;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 0px;
    padding: 0px 75px;
    border-bottom: 1px solid lightgrey;
    background-color: white;
}

.NavBarForm_NavBar__3Cyof h1 {
    font-family: "montserrat";
    font-size: 28px;
    color: #4a4a4f;
}

.NavBarForm_NavBar__3Cyof span {
    font-family: "montserrat";
    font-size: 14px;
    color: #4a4a4f;
    margin-right: 15px;
    font-weight: bold;
}

.NavBarForm_NavBar__3Cyof > div {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-right: 30px;
}

.NavBarForm_NavBar__3Cyof form {
    display: flex;
    flex-direction: row;
}

.NavBarForm_NavBar__3Cyof label {
    color: #4a4a4f;
    margin: 1px;
    padding: 5px 0 5px;
    padding-right: 10px;
}

.NavBarForm_NavBar__3Cyof input {
    margin: 1px;
    margin-right: 10px;
    width: 150px;
    border: 1px solid lightgrey;
    padding: 2px 5px;
    background-color: white;
}

.NavBarForm_NavBar__3Cyof form > div {
    display: flex;
    justify-content: space-between;
    padding: 0px 10px;
}

.NavBarForm_NavBar__3Cyof #NavBarForm_login-form__1oA-3 > div{
    display: inline-block;
}

.NavBarForm_NavBar__3Cyof button {
    color: #4a4a4f;
    border: lightgrey solid 1px;
    background-color: white;
    padding: 5px;
    width: 100px;
    margin-left: 5px;
}
.NavBarForm_NavBar__3Cyof button:hover{
    color: #4a4a4f;
    background-color: #e6ecf0;
    border: lightgrey solid 1px;
}
.Sidebar_sidebar__2k4iu {
    grid-row: 2;
    grid-column: 1;
    display: flex;
    flex-direction: column;
    padding: 10px 0;
    text-align: left;
    padding-left: 40px;
}

.Sidebar_sidebar__2k4iu > a {
    background-color: white;
    color: #445261;
    border: white solid 1px;
    padding: 10px 0;
    padding-left: 10px;
    margin-left: 30px;
}

.Sidebar_sidebar__2k4iu > a:hover {
    color: #445261;
    background-color: #e6ecf0;
    border: white solid 1px;
}

.Content_SearchItem__2jWwB {
    display: flex;
    flex-direction: column;
    margin: 10px 0px;
    border: 1px solid rgb(238, 238, 238);
    background-color: white;
    max-width: 80%;
    box-shadow: 0px 10px 30px rgb(238, 238, 238);
}

.Content_PostImage__1z-1f {
    max-width: 100%;
    height: auto;
    border-radius: 5px;
}

.Content_Content__3bwKI {
    grid-row: 2;
    grid-column: 2;
    padding: 5px;
    padding-right: 60px;
}

.Content_Content__3bwKI h1 {
    color: #4a4a4f;
    font-size: 20px;
    text-align: left;
}

.Content_TitleBar__bphcu {
    margin-top: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.Content_TitleBar__bphcu form {
    text-align: right;
    font-family: montserrat;
    color: #4a4a4f;
}

.Content_TitleBar__bphcu form input {
    border: 1px solid lightgrey;
    margin-left: 10px;
    margin-bottom: 7px;
}

.Content_TitleBar__bphcu h1 {
    margin-top: 0;
}

.Content_Content__3bwKI h4 {
    font-size: 14px;
    color: #009fe3;
    text-align: right;
    margin-top: 0;
}

.Content_SearchResults__24hxl {
    display: flex;
    flex-direction: column;
    margin: 0 10%;
}

.Loader_Loader__vKy85 {
    display: inline-block;
    width: 80px;
    height: 80px;
    margin: 20% 35%;
  }
  .Loader_Loader__vKy85:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #000;
    border-color: #000 transparent #000 transparent;
    animation: Loader_Loader__vKy85 1.2s linear infinite;
  }
  @keyframes Loader_Loader__vKy85 {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
